import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import "../src/styles/index.scss"
import {Provider} from "react-redux";
import {store} from "./store/store";


ReactDOM.render(
  <React.StrictMode>
      <Provider store={store}>
          <App />
      </Provider>
  </React.StrictMode>,
  document.getElementById('body')
);
